import React from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CFormLabel,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilUser } from '@coreui/icons'
import { useState } from 'react'

const Availability = () => {
  const [startX, setStartX] = useState('')
  const [startY, setStartY] = useState('')
  const [startXY, setStartXY] = useState('')
  const [targetX, setTargetX] = useState('')
  const [targetY, setTargetY] = useState('')
  const [targetXY, setTargetXY] = useState('')
  const [outpostX, setOutpostX] = useState('')
  const [outpostY, setOutpostY] = useState('')
  const [outpostXY, setOutpostXY] = useState('')
  const [interferenceX, setInterferenceX] = useState('')
  const [interferenceY, setInterferenceY] = useState('')
  const [interferenceXY, setInterferenceXY] = useState('')
  const [statusMessage, setStatusMessage] = useState('请输入坐标并提交')
  const [statusStyle, setStatusStyle] = useState('text-white bg-primary py-5')
  const [offset, setOffset] = useState('')

  const distance = (ax, ay, bx, by) => {
    return Math.sqrt(Math.pow(ax - bx, 2) + Math.pow(ay - by, 2))
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    var sx = Number(startX)
    var sy = Number(startY)
    var tx = Number(targetX)
    var ty = Number(targetY)
    var ox = Number(outpostX)
    var oy = Number(outpostY)
    var ix = Number(interferenceX)
    var iy = Number(interferenceY)
    var a = distance(sx, sy, ox, oy)
    var b = distance(ox, oy, tx, ty)
    var c = distance(sx, sy, ix, iy)
    var d = distance(ix, iy, tx, ty)
    var e = distance(sx, sy, tx, ty)
    var notInterfered = a / 5 + b < c / 5 + d
    var isOutpostTooFar = a / 5 + b > e
    if (notInterfered && !isOutpostTooFar) {
      setStatusStyle('text-white bg-success py-5')
      setStatusMessage('报告首长，天气晴朗，可以起飞！')
    } else if (isOutpostTooFar) {
      setStatusStyle('text-white bg-warning py-5')
      setStatusMessage('报告首长，前哨太远，无法起飞！')
    } else {
      setStatusStyle('text-white bg-danger py-5')
      setStatusMessage('很遗憾，所选位置无法起飞=_=')
    }

    var offset =
      Math.abs((sy - oy) * tx + (ox - sx) * ty + sx * oy - ox * sy) /
      Math.sqrt(Math.pow(oy - sy, 2) + Math.pow(ox - sx, 2))
    setOffset(offset.toFixed(2))

    console.log(a)
    console.log(b)
    console.log(c)
    console.log(d)
  }

  const setStartXYFunc = (event) => {
    setStartXY(event)
    var xy = event.replace(/\s*/g, '').replace(/\(|\)/g, '').split(/[,，]/)
    setStartX(xy[0])
    if (xy.length > 1) {
      setStartY(xy[1])
    }
  }

  const setTargetXYFunc = (event) => {
    setTargetXY(event)
    var xy = event.replace(/\s*/g, '').replace(/\(|\)/g, '').split(/[,，]/)
    setTargetX(xy[0])
    if (xy.length > 1) {
      setTargetY(xy[1])
    }
  }

  const setOutpostXYFunc = (event) => {
    setOutpostXY(event)
    var xy = event.replace(/\s*/g, '').replace(/\(|\)/g, '').split(/[,，]/)
    setOutpostX(xy[0])
    if (xy.length > 1) {
      setOutpostY(xy[1])
    }
  }

  const setInterferenceXYFunc = (event) => {
    setInterferenceXY(event)
    var xy = event.replace(/\s*/g, '').replace(/\(|\)/g, '').split(/[,，]/)
    setInterferenceX(xy[0])
    if (xy.length > 1) {
      setInterferenceY(xy[1])
    }
  }

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={8}>
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm onSubmit={handleSubmit}>
                    <h1>空降坐标检测器</h1>
                    <p className="text-medium-emphasis">请输入坐标检测是否可以空降</p>
                    <CRow className="mb-3">
                      <CFormLabel className="col-sm-4 col-form-label">起飞点坐标</CFormLabel>
                      <CCol sm={8}>
                        <CInputGroup>
                          <CFormInput
                            id="startXY"
                            required
                            value={startXY}
                            onChange={(event) => setStartXYFunc(event.target.value)}
                          />
                        </CInputGroup>
                      </CCol>
                    </CRow>
                    <CRow className="mb-3">
                      <CFormLabel className="col-sm-4 col-form-label">落点坐标</CFormLabel>
                      <CCol sm={8}>
                        <CInputGroup>
                          <CFormInput
                            id="targetXY"
                            required
                            value={targetXY}
                            onChange={(event) => setTargetXYFunc(event.target.value)}
                          />
                        </CInputGroup>
                      </CCol>
                    </CRow>
                    <CRow className="mb-3">
                      <CFormLabel className="col-sm-4 col-form-label">前哨坐标</CFormLabel>
                      <CCol sm={8}>
                        <CInputGroup>
                          <CFormInput
                            id="outpostXY"
                            required
                            value={outpostXY}
                            onChange={(event) => setOutpostXYFunc(event.target.value)}
                          />
                        </CInputGroup>
                      </CCol>
                    </CRow>
                    <CRow className="mb-3">
                      <CFormLabel className="col-sm-4 col-form-label">干扰点坐标</CFormLabel>
                      <CCol sm={8}>
                        <CInputGroup>
                          <CFormInput
                            id="interferenceXY"
                            required
                            value={interferenceXY}
                            onChange={(event) => setInterferenceXYFunc(event.target.value)}
                          />
                        </CInputGroup>
                      </CCol>
                    </CRow>
                    <br />
                    <CRow>
                      <div className="d-grid gap-2 col-8 mx-auto">
                        <CButton color="primary" className="px-4" type="submit">
                          提交
                        </CButton>
                      </div>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className={statusStyle}>
                <CCardBody className="text-center">
                  <div>
                    <h2>检测结果</h2>
                    <br />
                    <p>{statusMessage}</p>
                  </div>
                  <div>
                    <h2>偏移距离</h2>
                    <p>偏移距离：{offset} 吉米</p>
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Availability
